@use "../utils/variables";

// styling common to the toggled mobile and tablet/desktop menus

.main-nav {

  @media (min-width: variables.$md-breakPt) {
    height: 100vh;
  }

  &__content {
    display: flex;
    flex-direction: column;
  }

  &__item {
    font-size: 2rem;
    font-weight: 600;
  }

  &__social {
    display: flex;
    flex-direction: column;
    margin-top: 2rem;
  }

  &__social-link {
    color: variables.$med-gray;
    &:first-child {
      margin-bottom: 0.5rem;
    }
  }
  svg {
    margin-right: 1rem;
  }
}
